<template>
  <div>
    <v-container>
      <v-card>
        <v-tabs v-model="tab" background-color="teal" dark>
          <v-tab href="#login">Login</v-tab>
          <v-tab href="#register">Register</v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item value="register">
            <v-card-title>Register</v-card-title>
            <v-card-text>
              <v-container>
                <v-form>
                  <v-text-field
                    type="email"
                    v-model="user.email"
                    label="Email"
                    color="teal"
                    required
                    autofocus
                  ></v-text-field>
                  <v-text-field
                    type="password"
                    autocomplete="password"
                    v-model="registerPassword"
                    label="Password"
                    color="teal"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="user.forename"
                    label="Forename"
                    color="teal"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="user.surname"
                    label="Surname"
                    color="teal"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="user.telephone"
                    label="Mobile Number"
                    color="teal"
                    required
                  ></v-text-field>
                  <v-radio-group v-model="user.role" row>
                    <v-radio
                      color="teal"
                      label="Landlord"
                      value="landlord"
                    ></v-radio>
                    <v-radio
                      color="#eea86e"
                      label="Tenant"
                      value="tenant"
                    ></v-radio>
                  </v-radio-group>
                </v-form>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-container>
                <v-btn
                  @click="register"
                  :loading="isLoggingIn"
                  :disabled="isLoggingIn"
                  color="teal"
                  dark
                  class="ma-2"
                >
                  Register
                  <v-icon right dark>fa fa-sign-in-alt</v-icon>
                </v-btn>
              </v-container>
            </v-card-actions>
          </v-tab-item>

          <v-tab-item value="login">
            <v-card-title>Login</v-card-title>
            <v-card-text>
              <v-container>
                <v-form>
                  <v-text-field
                    type="email"
                    v-model="loginEmail"
                    label="Email"
                    color="teal"
                    required
                    autofocus
                  ></v-text-field>
                  <v-text-field
                    type="password"
                    autocomplete="password"
                    v-model="loginPassword"
                    label="Password"
                    color="teal"
                    required
                  ></v-text-field>
                </v-form>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-container>
                <v-btn
                  @click="login"
                  :loading="isLoggingIn"
                  :disabled="isLoggingIn"
                  color="teal"
                  dark
                  class="ma-2"
                >
                  Login
                  <v-icon right dark>fa fa-sign-in-alt</v-icon>
                </v-btn>
              </v-container>
            </v-card-actions>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import firebase from "firebase";
import db from "./firebaseInit";

export default {
  name: "register",
  data() {
    return {
      registerPassword: "",
      user: { role: "landlord" },
      loginEmail: "",
      loginPassword: "",
      isLoggingIn: false,
      tab: null
    };
  },
  methods: {
    register(e) {
      e.preventDefault();
      this.isLoggingIn = true;
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.user.email, this.registerPassword)
        .then(
          user => {
            db.collection("users")
              .doc(firebase.auth().currentUser.uid)
              .set(this.user)
              .then(() => {
                this.isLoggingIn = false;
                this.$router.push("/dashboard");
              })
              .catch(err => console.log(err));
          },
          err => {
            alert("err: " + err.message);
            this.isLoggingIn = false;
          }
        )
        .catch(err => {
          alert("catch: " + err);
          this.isLoggingIn = false;
        });
    },
    login(e) {
      e.preventDefault();
      firebase
        .auth()
        .signInWithEmailAndPassword(this.loginEmail, this.loginPassword)
        .then(
          user => this.$router.push("/dashboard"),
          err => alert(err.message)
        );
    }
  }
};
</script>

<style></style>



